/* Variables CSS */
:root {
  /* Colores principales */
  --primary: #4f46e5;
  --primary-light: #6366f1;
  --primary-dark: #4338ca;
  --secondary: #06b6d4;
  --success: #10b981;
  --warning: #f59e0b;
  --danger: #ef4444;
  --info: #3b82f6;
  
  /* Colores neutros */
  --gray-50: #f9fafb;
  --gray-100: #f3f4f6;
  --gray-200: #e5e7eb;
  --gray-300: #d1d5db;
  --gray-400: #9ca3af;
  --gray-500: #6b7280;
  --gray-600: #4b5563;
  --gray-700: #374151;
  --gray-800: #1f2937;
  --gray-900: #111827;
  
  /* Colores de categorías */
  --category-dev: #3b82f6;
  --category-support: #10b981;
  --category-meeting: #f59e0b;
  --category-training: #8b5cf6;
  --category-research: #ef4444;
  --category-other: #6b7280;
  
  /* Sombras */
  --shadow-sm: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
  --shadow-md: 0 4px 6px -1px rgba(0, 0, 0, 0.1);
  --shadow-lg: 0 10px 15px -3px rgba(0, 0, 0, 0.1);
  --shadow-xl: 0 20px 25px -5px rgba(0, 0, 0, 0.1);
  --shadow-2xl: 0 25px 50px -12px rgba(0, 0, 0, 0.25);
  
  /* Bordes */
  --radius-sm: 0.375rem;
  --radius-md: 0.5rem;
  --radius-lg: 0.75rem;
  --radius-xl: 1rem;
  --radius-2xl: 1.5rem;
  
  /* Espaciados */
  --space-xs: 0.25rem;
  --space-sm: 0.5rem;
  --space-md: 1rem;
  --space-lg: 1.5rem;
  --space-xl: 2rem;
  --space-2xl: 3rem;
  
  /* Tipografía */
  --font-sans: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
  --font-mono: 'SF Mono', Monaco, 'Cascadia Mono', 'Segoe UI Mono', monospace;
}

/* Reset y estilos base */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: var(--font-sans);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  min-height: 100vh;
  color: var(--gray-800);
  line-height: 1.6;
}

/* Scrollbar personalizada */
::-webkit-scrollbar {
  width: 10px;
  height: 10px;
}

::-webkit-scrollbar-track {
  background: var(--gray-100);
  border-radius: var(--radius-lg);
}

::-webkit-scrollbar-thumb {
  background: var(--primary-light);
  border-radius: var(--radius-lg);
  transition: background 0.3s ease;
}

::-webkit-scrollbar-thumb:hover {
  background: var(--primary-dark);
}

/* App principal */
.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

/* Header */
.app-header {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  position: sticky;
  top: 0;
  z-index: 1000;
}

.header-gradient {
  background: linear-gradient(135deg, rgba(79, 70, 229, 0.9) 0%, rgba(99, 102, 241, 0.9) 100%);
  padding: var(--space-md) 0;
}

.header-content {
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 var(--space-xl);
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: var(--space-xl);
}

.logo-container {
  display: flex;
  align-items: center;
  gap: var(--space-md);
}

.logo-circle {
  width: 60px;
  height: 60px;
  background: white;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: var(--shadow-lg);
  animation: pulse 2s infinite;
}

@keyframes pulse {
  0%, 100% {
    box-shadow: 0 0 0 0 rgba(255, 255, 255, 0.4);
  }
  50% {
    box-shadow: 0 0 0 10px rgba(255, 255, 255, 0);
  }
}

.logo-icon {
  font-size: 2rem;
  color: var(--primary);
}

.logo-text h1 {
  color: white;
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: var(--space-xs);
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.2);
}

.subtitle {
  color: rgba(255, 255, 255, 0.9);
  font-size: 0.9rem;
  font-weight: 500;
}

.header-controls {
  display: flex;
  align-items: center;
}

.date-navigation {
  display: flex;
  align-items: center;
  gap: var(--space-md);
  background: rgba(255, 255, 255, 0.15);
  padding: var(--space-sm) var(--space-md);
  border-radius: var(--radius-xl);
  backdrop-filter: blur(10px);
}

.date-nav-btn {
  background: rgba(255, 255, 255, 0.2);
  border: none;
  width: 36px;
  height: 36px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  color: white;
  font-size: 1.2rem;
  transition: all 0.3s ease;
}

.date-nav-btn:hover {
  background: rgba(255, 255, 255, 0.3);
  transform: scale(1.1);
}

.current-date {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: var(--space-xs);
  min-width: 200px;
}

.date-display {
  color: white;
  font-size: 0.95rem;
  font-weight: 600;
  text-align: center;
}

.today-btn {
  background: white;
  color: var(--primary);
  border: none;
  padding: var(--space-xs) var(--space-md);
  border-radius: var(--radius-lg);
  font-size: 0.85rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.today-btn:hover {
  background: var(--gray-100);
  transform: translateY(-1px);
  box-shadow: var(--shadow-md);
}

/* Contenedor principal */
.app-container {
  display: flex;
  flex: 1;
  max-width: 1400px;
  margin: 0 auto;
  width: 100%;
  padding: var(--space-xl);
  gap: var(--space-xl);
}

/* Sidebar */
.app-sidebar {
  width: 280px;
  flex-shrink: 0;
}

.sidebar-content {
  background: rgba(255, 255, 255, 0.95);
  border-radius: var(--radius-2xl);
  padding: var(--space-xl);
  box-shadow: var(--shadow-xl);
  backdrop-filter: blur(10px);
  height: calc(100vh - 160px);
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.main-nav {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: var(--space-xl);
}

.nav-section {
  display: flex;
  flex-direction: column;
  gap: var(--space-sm);
}

.nav-title {
  font-size: 0.75rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  color: var(--gray-500);
  margin-bottom: var(--space-xs);
}

.nav-item {
  display: flex;
  align-items: center;
  gap: var(--space-md);
  padding: var(--space-md);
  border: none;
  background: transparent;
  border-radius: var(--radius-lg);
  cursor: pointer;
  transition: all 0.3s ease;
  color: var(--gray-700);
  font-size: 0.95rem;
  font-weight: 500;
  text-align: left;
  position: relative;
}

.nav-item:hover {
  background: var(--gray-100);
  color: var(--primary);
  transform: translateX(5px);
}

.nav-item.active {
  background: linear-gradient(135deg, var(--primary) 0%, var(--primary-light) 100%);
  color: white;
  box-shadow: var(--shadow-md);
}

.nav-icon {
  font-size: 1.2rem;
  width: 24px;
  text-align: center;
}

.nav-text {
  flex: 1;
}

.nav-badge {
  background: var(--danger);
  color: white;
  font-size: 0.75rem;
  padding: 2px 8px;
  border-radius: 20px;
  font-weight: 600;
  min-width: 24px;
  text-align: center;
}

.export-btn {
  background: linear-gradient(135deg, #10b981 0%, #059669 100%);
  color: white;
}

.export-btn:hover {
  background: linear-gradient(135deg, #059669 0%, #047857 100%);
}

.category-filter {
  width: 100%;
  padding: var(--space-md);
  border: 2px solid var(--gray-200);
  border-radius: var(--radius-lg);
  background: white;
  color: var(--gray-700);
  font-size: 0.95rem;
  cursor: pointer;
  transition: all 0.3s ease;
}

.category-filter:focus {
  outline: none;
  border-color: var(--primary);
  box-shadow: 0 0 0 3px rgba(79, 70, 229, 0.1);
}

.sidebar-footer {
  margin-top: auto;
  padding-top: var(--space-xl);
  border-top: 1px solid var(--gray-200);
}

.quick-stats {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: var(--space-md);
}

.quick-stat {
  text-align: center;
  padding: var(--space-md);
  background: linear-gradient(135deg, var(--gray-50) 0%, white 100%);
  border-radius: var(--radius-lg);
  box-shadow: var(--shadow-sm);
}

.quick-stat-value {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--primary);
  margin-bottom: var(--space-xs);
}

.quick-stat-label {
  font-size: 0.85rem;
  color: var(--gray-500);
  font-weight: 500;
}

/* Contenido principal */
.app-main {
  flex: 1;
  min-width: 0;
}

/* Contenedor de tareas */
.tasks-container {
  display: flex;
  flex-direction: column;
  gap: var(--space-xl);
}

.tasks-header {
  background: white;
  border-radius: var(--radius-2xl);
  padding: var(--space-xl);
  box-shadow: var(--shadow-xl);
}

.tasks-header h2 {
  color: var(--gray-800);
  font-size: 1.75rem;
  font-weight: 700;
  margin-bottom: var(--space-md);
  background: linear-gradient(135deg, var(--primary) 0%, var(--secondary) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.tasks-summary {
  display: flex;
  gap: var(--space-xl);
}

.summary-item {
  font-size: 1rem;
  color: var(--gray-600);
}

.summary-item strong {
  color: var(--primary);
  font-weight: 700;
}

/* Formulario de tareas */
.tasks-content {
  display: flex;
  flex-direction: column;
  gap: var(--space-xl);
}

.task-form-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: var(--space-xl);
}

.form-card {
  background: white;
  border-radius: var(--radius-2xl);
  padding: var(--space-xl);
  box-shadow: var(--shadow-xl);
  transition: transform 0.3s ease;
}

.form-card:hover {
  transform: translateY(-2px);
}

.form-title {
  display: flex;
  align-items: center;
  gap: var(--space-md);
  color: var(--gray-800);
  font-size: 1.25rem;
  font-weight: 600;
  margin-bottom: var(--space-xl);
}

.form-icon {
  font-size: 1.5rem;
}

.form-content {
  display: flex;
  flex-direction: column;
  gap: var(--space-lg);
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: var(--space-sm);
}

.form-label {
  display: flex;
  align-items: center;
  gap: var(--space-sm);
  font-size: 0.95rem;
  font-weight: 600;
  color: var(--gray-700);
}

.label-icon {
  font-size: 1.1rem;
}

.form-textarea {
  padding: var(--space-md);
  border: 2px solid var(--gray-200);
  border-radius: var(--radius-lg);
  font-size: 1rem;
  font-family: var(--font-sans);
  resize: vertical;
  min-height: 100px;
  transition: all 0.3s ease;
  background: white;
}

.form-textarea:focus {
  outline: none;
  border-color: var(--primary);
  box-shadow: 0 0 0 3px rgba(79, 70, 229, 0.1);
}

.form-row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: var(--space-md);
}

.hours-input-container {
  position: relative;
}

.form-input {
  width: 100%;
  padding: var(--space-md);
  border: 2px solid var(--gray-200);
  border-radius: var(--radius-lg);
  font-size: 1rem;
  transition: all 0.3s ease;
  background: white;
}

.form-input:focus {
  outline: none;
  border-color: var(--primary);
  box-shadow: 0 0 0 3px rgba(79, 70, 229, 0.1);
}

.input-suffix {
  position: absolute;
  right: var(--space-md);
  top: 50%;
  transform: translateY(-50%);
  color: var(--gray-500);
  font-size: 0.9rem;
}

.form-select {
  width: 100%;
  padding: var(--space-md);
  border: 2px solid var(--gray-200);
  border-radius: var(--radius-lg);
  font-size: 1rem;
  background: white;
  cursor: pointer;
  transition: all 0.3s ease;
}

.form-select:focus {
  outline: none;
  border-color: var(--primary);
  box-shadow: 0 0 0 3px rgba(79, 70, 229, 0.1);
}

.submit-btn {
  background: linear-gradient(135deg, var(--primary) 0%, var(--primary-light) 100%);
  color: white;
  border: none;
  padding: var(--space-md) var(--space-xl);
  border-radius: var(--radius-lg);
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: var(--space-sm);
  transition: all 0.3s ease;
  margin-top: var(--space-sm);
}

.submit-btn:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg);
}

.submit-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.btn-icon {
  font-size: 1.2rem;
}

/* Balance Container */
.balance-container {
  background: white;
  border-radius: var(--radius-2xl);
  padding: var(--space-xl);
  box-shadow: var(--shadow-xl);
}

.balance-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: var(--space-xl);
}

.balance-title {
  display: flex;
  align-items: center;
  gap: var(--space-md);
  color: var(--gray-800);
  font-size: 1.25rem;
  font-weight: 600;
}

.month-label {
  background: linear-gradient(135deg, var(--primary) 0%, var(--primary-light) 100%);
  color: white;
  padding: var(--space-xs) var(--space-md);
  border-radius: var(--radius-lg);
  font-size: 0.9rem;
  font-weight: 600;
}

.progress-section {
  margin-bottom: var(--space-xl);
}

.progress-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: var(--space-sm);
}

.progress-label {
  font-size: 0.95rem;
  font-weight: 600;
  color: var(--gray-700);
}

.progress-percentage {
  font-size: 1.1rem;
  font-weight: 700;
}

.progress-bar-container {
  position: relative;
}

.progress-bar {
  height: 12px;
  background: var(--gray-200);
  border-radius: var(--radius-lg);
  overflow: hidden;
  margin-bottom: var(--space-xs);
}

.progress-fill {
  height: 100%;
  border-radius: var(--radius-lg);
  transition: width 0.5s ease;
}

.progress-labels {
  display: flex;
  justify-content: space-between;
  font-size: 0.8rem;
  color: var(--gray-500);
}

.stats-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: var(--space-md);
  margin-bottom: var(--space-xl);
}

.stat-card {
  display: flex;
  align-items: center;
  gap: var(--space-md);
  padding: var(--space-md);
  background: var(--gray-50);
  border-radius: var(--radius-lg);
  border: 1px solid var(--gray-200);
  transition: transform 0.3s ease;
}

.stat-card:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
}

.stat-icon {
  width: 48px;
  height: 48px;
  border-radius: var(--radius-lg);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
}

.stat-content {
  flex: 1;
}

.stat-value {
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: var(--space-xs);
}

.stat-label {
  font-size: 0.85rem;
  color: var(--gray-500);
  font-weight: 500;
}

.weekly-section {
  background: linear-gradient(135deg, var(--gray-50) 0%, white 100%);
  border-radius: var(--radius-lg);
  padding: var(--space-md);
  border: 1px solid var(--gray-200);
}

.weekly-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: var(--space-sm);
  font-size: 0.95rem;
  font-weight: 600;
  color: var(--gray-700);
}

.weekly-hours {
  font-size: 1.1rem;
  font-weight: 700;
  color: var(--primary);
}

.weekly-progress {
  height: 6px;
  background: var(--gray-200);
  border-radius: var(--radius-lg);
  overflow: hidden;
}

.weekly-progress-bar {
  height: 100%;
  background: linear-gradient(135deg, var(--secondary) 0%, #0891b2 100%);
  border-radius: var(--radius-lg);
  transition: width 0.5s ease;
}

/* Lista de tareas */
.tasks-list-container {
  background: white;
  border-radius: var(--radius-2xl);
  padding: var(--space-xl);
  box-shadow: var(--shadow-xl);
}

.list-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: var(--space-xl);
}

.list-header h3 {
  color: var(--gray-800);
  font-size: 1.25rem;
  font-weight: 600;
}

.date-label {
  background: var(--gray-100);
  color: var(--gray-600);
  padding: var(--space-xs) var(--space-md);
  border-radius: var(--radius-lg);
  font-size: 0.9rem;
  font-weight: 500;
}

.empty-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: var(--space-2xl);
  text-align: center;
  color: var(--gray-400);
}

.empty-icon {
  font-size: 4rem;
  margin-bottom: var(--space-md);
  opacity: 0.5;
}

.empty-text h4 {
  font-size: 1.25rem;
  font-weight: 600;
  margin-bottom: var(--space-xs);
  color: var(--gray-500);
}

.empty-text p {
  font-size: 1rem;
  color: var(--gray-400);
}

.tasks-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: var(--space-md);
}

.task-card {
  background: white;
  border-radius: var(--radius-xl);
  padding: var(--space-md);
  border: 2px solid var(--gray-200);
  transition: all 0.3s ease;
  cursor: pointer;
}

.task-card:hover {
  border-color: var(--primary-light);
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg);
}

.task-card.completed {
  opacity: 0.7;
  border-color: var(--success);
  background: linear-gradient(135deg, rgba(16, 185, 129, 0.05) 0%, white 100%);
}

.task-card-content {
  display: flex;
  flex-direction: column;
  gap: var(--space-md);
}

.task-card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.task-checkbox {
  width: 20px;
  height: 20px;
  cursor: pointer;
  accent-color: var(--success);
}

.task-category-badge {
  padding: var(--space-xs) var(--space-md);
  border-radius: var(--radius-lg);
  font-size: 0.8rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

.task-description {
  font-size: 0.95rem;
  color: var(--gray-700);
  line-height: 1.6;
}

.task-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.task-hours-display {
  display: flex;
  align-items: center;
  gap: var(--space-xs);
  font-size: 0.9rem;
  font-weight: 600;
  color: var(--gray-600);
}

.hours-icon {
  font-size: 1rem;
}

.hours-value {
  color: var(--primary);
}

.task-actions {
  display: flex;
  gap: var(--space-xs);
}

.task-action-btn {
  width: 32px;
  height: 32px;
  border-radius: var(--radius-md);
  border: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1rem;
  transition: all 0.3s ease;
}

.edit-btn {
  background: var(--gray-100);
  color: var(--gray-600);
}

.edit-btn:hover {
  background: var(--info);
  color: white;
  transform: scale(1.1);
}

.delete-btn {
  background: var(--gray-100);
  color: var(--gray-600);
}

.delete-btn:hover {
  background: var(--danger);
  color: white;
  transform: scale(1.1);
}

/* Modal */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(5px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2000;
  animation: fadeIn 0.3s ease;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

.modal-content {
  background: white;
  border-radius: var(--radius-2xl);
  width: 90%;
  max-width: 500px;
  max-height: 90vh;
  overflow-y: auto;
  box-shadow: var(--shadow-2xl);
  animation: slideUp 0.3s ease;
}

@keyframes slideUp {
  from { transform: translateY(20px); opacity: 0; }
  to { transform: translateY(0); opacity: 1; }
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: var(--space-xl);
  border-bottom: 1px solid var(--gray-200);
}

.modal-header h3 {
  color: var(--gray-800);
  font-size: 1.25rem;
  font-weight: 600;
}

.modal-close {
  background: none;
  border: none;
  font-size: 1.5rem;
  color: var(--gray-500);
  cursor: pointer;
  transition: color 0.3s ease;
}

.modal-close:hover {
  color: var(--danger);
}

.modal-body {
  padding: var(--space-xl);
}

.modal-body .form-group {
  margin-bottom: var(--space-md);
}

.modal-footer {
  display: flex;
  gap: var(--space-md);
  padding: var(--space-xl);
  border-top: 1px solid var(--gray-200);
  justify-content: flex-end;
}

.btn-secondary, .btn-primary {
  padding: var(--space-md) var(--space-xl);
  border-radius: var(--radius-lg);
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  border: none;
}

.btn-secondary {
  background: var(--gray-200);
  color: var(--gray-700);
}

.btn-secondary:hover {
  background: var(--gray-300);
}

.btn-primary {
  background: linear-gradient(135deg, var(--primary) 0%, var(--primary-light) 100%);
  color: white;
}

.btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg);
}

/* Calendario */
.calendar-container-full {
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: var(--space-xl);
}

.calendar-main {
  background: white;
  border-radius: var(--radius-2xl);
  padding: var(--space-xl);
  box-shadow: var(--shadow-xl);
}

.calendar-container {
  background: white;
  border-radius: var(--radius-xl);
  padding: var(--space-lg);
}

.calendar-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: var(--space-xl);
}

.nav-arrow {
  background: var(--gray-100);
  border: none;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  color: var(--gray-700);
  font-size: 1.5rem;
  transition: all 0.3s ease;
}

.nav-arrow:hover {
  background: var(--primary);
  color: white;
  transform: scale(1.1);
}

.calendar-title {
  color: var(--gray-800);
  font-size: 1.25rem;
  font-weight: 600;
  text-align: center;
}

.calendar-weekdays {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap: var(--space-xs);
  margin-bottom: var(--space-md);
  text-align: center;
}

.weekday {
  font-size: 0.9rem;
  font-weight: 600;
  color: var(--gray-600);
  padding: var(--space-sm) 0;
}

.weekday.weekend {
  color: var(--danger);
}

.calendar-grid {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap: var(--space-xs);
}

.calendar-day {
  aspect-ratio: 1;
  border: 2px solid var(--gray-200);
  border-radius: var(--radius-lg);
  padding: var(--space-sm);
  cursor: pointer;
  display: flex;
  flex-direction: column;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.calendar-day:hover {
  border-color: var(--primary-light);
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
}

.calendar-day.selected {
  border-color: var(--primary);
  background: linear-gradient(135deg, rgba(79, 70, 229, 0.1) 0%, white 100%);
}

.calendar-day.today {
  border-color: var(--success);
  background: linear-gradient(135deg, rgba(16, 185, 129, 0.1) 0%, white 100%);
}

.calendar-day.other-month {
  opacity: 0.4;
  background: var(--gray-100);
}

.calendar-day-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: var(--space-xs);
}

.calendar-day-number {
  font-size: 0.9rem;
  font-weight: 600;
  color: var(--gray-700);
}

.today-badge {
  background: var(--success);
  color: white;
  font-size: 0.6rem;
  padding: 1px 4px;
  border-radius: var(--radius-sm);
  font-weight: 600;
}

.calendar-hours-indicator {
  flex: 1;
  display: flex;
  align-items: flex-end;
  margin: var(--space-xs) 0;
  position: relative;
}

.hours-bar {
  width: 100%;
  border-radius: var(--radius-sm);
  transition: height 0.3s ease;
}

.hours-text {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  text-align: center;
  font-size: 0.7rem;
  font-weight: 600;
  color: white;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
  padding: 1px;
}

.calendar-tasks-dots {
  display: flex;
  justify-content: center;
  gap: 2px;
  margin-top: var(--space-xs);
}

.task-dot {
  width: 6px;
  height: 6px;
  border-radius: 50%;
}

/* Estadísticas */
.stats-container {
  background: white;
  border-radius: var(--radius-2xl);
  padding: var(--space-xl);
  box-shadow: var(--shadow-xl);
}

.stats-main-container {
  display: flex;
  flex-direction: column;
  gap: var(--space-xl);
}

.stats-overview {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: var(--space-md);
}

.overview-card {
  background: linear-gradient(135deg, var(--gray-50) 0%, white 100%);
  border-radius: var(--radius-xl);
  padding: var(--space-lg);
  display: flex;
  align-items: center;
  gap: var(--space-md);
  border: 1px solid var(--gray-200);
  transition: transform 0.3s ease;
}

.overview-card:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg);
}

.overview-icon {
  font-size: 2.5rem;
  width: 60px;
  height: 60px;
  background: white;
  border-radius: var(--radius-lg);
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: var(--shadow-md);
}

.overview-content {
  flex: 1;
}

.overview-value {
  font-size: 2rem;
  font-weight: 700;
  color: var(--primary);
  line-height: 1;
  margin-bottom: var(--space-xs);
}

.overview-label {
  font-size: 1rem;
  font-weight: 600;
  color: var(--gray-700);
  margin-bottom: var(--space-xs);
}

.overview-detail {
  font-size: 0.85rem;
  color: var(--gray-500);
}

.category-breakdown {
  background: var(--gray-50);
  border-radius: var(--radius-xl);
  padding: var(--space-xl);
  border: 1px solid var(--gray-200);
}

.section-title {
  color: var(--gray-800);
  font-size: 1.25rem;
  font-weight: 600;
  margin-bottom: var(--space-lg);
}

.category-chart {
  display: flex;
  flex-direction: column;
  gap: var(--space-md);
}

.category-bar-container {
  display: flex;
  flex-direction: column;
  gap: var(--space-xs);
}

.category-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.category-name {
  display: flex;
  align-items: center;
  gap: var(--space-sm);
  font-size: 0.95rem;
  font-weight: 600;
  color: var(--gray-700);
}

.category-color {
  width: 12px;
  height: 12px;
  border-radius: 50%;
}

.category-stats {
  display: flex;
  gap: var(--space-md);
}

.category-hours {
  font-weight: 600;
  color: var(--gray-800);
}

.category-percentage {
  font-weight: 600;
  color: var(--primary);
}

.category-bar-wrapper {
  height: 8px;
  background: var(--gray-200);
  border-radius: var(--radius-lg);
  overflow: hidden;
}

.category-bar {
  height: 100%;
  border-radius: var(--radius-lg);
  transition: width 0.5s ease;
}

.productivity-metrics {
  background: white;
  border-radius: var(--radius-xl);
  padding: var(--space-xl);
  border: 1px solid var(--gray-200);
}

.metrics-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: var(--space-md);
}

.metric-card {
  background: linear-gradient(135deg, var(--gray-50) 0%, white 100%);
  border-radius: var(--radius-lg);
  padding: var(--space-lg);
  display: flex;
  align-items: center;
  gap: var(--space-md);
  border: 1px solid var(--gray-200);
  transition: all 0.3s ease;
}

.metric-card:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
}

.metric-icon {
  font-size: 2rem;
  width: 50px;
  height: 50px;
  background: white;
  border-radius: var(--radius-lg);
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: var(--shadow-sm);
}

.metric-content {
  flex: 1;
}

.metric-value {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--primary);
  margin-bottom: var(--space-xs);
}

.metric-label {
  font-size: 0.9rem;
  color: var(--gray-600);
  font-weight: 500;
}

/* Configuración */
.settings-container {
  background: white;
  border-radius: var(--radius-2xl);
  padding: var(--space-xl);
  box-shadow: var(--shadow-xl);
}

.settings-card {
  max-width: 800px;
  margin: 0 auto;
}

.settings-title {
  color: var(--gray-800);
  font-size: 1.75rem;
  font-weight: 700;
  margin-bottom: var(--space-xl);
  background: linear-gradient(135deg, var(--primary) 0%, var(--secondary) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.settings-section {
  margin-bottom: var(--space-2xl);
  padding-bottom: var(--space-xl);
  border-bottom: 1px solid var(--gray-200);
}

.settings-section:last-child {
  border-bottom: none;
  margin-bottom: 0;
  padding-bottom: 0;
}

.settings-section h3 {
  display: flex;
  align-items: center;
  gap: var(--space-sm);
  color: var(--gray-800);
  font-size: 1.25rem;
  font-weight: 600;
  margin-bottom: var(--space-lg);
}

.goal-settings {
  display: flex;
  flex-direction: column;
  gap: var(--space-lg);
}

.goal-setting {
  display: flex;
  flex-direction: column;
  gap: var(--space-sm);
}

.goal-setting label {
  font-size: 0.95rem;
  font-weight: 600;
  color: var(--gray-700);
}

.goal-input-container {
  position: relative;
  max-width: 300px;
}

.goal-input-container input {
  width: 100%;
  padding: var(--space-md);
  border: 2px solid var(--gray-200);
  border-radius: var(--radius-lg);
  font-size: 1rem;
  transition: all 0.3s ease;
}

.goal-input-container input:focus {
  outline: none;
  border-color: var(--primary);
  box-shadow: 0 0 0 3px rgba(79, 70, 229, 0.1);
}

.goal-info {
  margin-top: var(--space-xs);
}

.info-text {
  font-size: 0.85rem;
  color: var(--gray-500);
}

.data-management {
  display: flex;
  flex-direction: column;
  gap: var(--space-md);
}

.danger-btn {
  background: linear-gradient(135deg, var(--danger) 0%, #dc2626 100%);
  color: white;
  border: none;
  padding: var(--space-md) var(--space-xl);
  border-radius: var(--radius-lg);
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: var(--space-sm);
  transition: all 0.3s ease;
  max-width: 300px;
}

.danger-btn:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg);
}

.warning-text {
  font-size: 0.85rem;
  color: var(--danger);
  max-width: 300px;
}

.report-settings {
  display: flex;
  flex-direction: column;
  gap: var(--space-md);
}

.setting-item {
  display: flex;
  align-items: center;
  gap: var(--space-md);
}

.setting-item label {
  display: flex;
  align-items: center;
  gap: var(--space-sm);
  font-size: 0.95rem;
  color: var(--gray-700);
  cursor: pointer;
}

.setting-item input[type="checkbox"] {
  width: 18px;
  height: 18px;
  cursor: pointer;
  accent-color: var(--primary);
}

.theme-settings {
  display: flex;
  flex-direction: column;
  gap: var(--space-md);
}

.theme-options {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: var(--space-md);
}

.theme-option {
  background: white;
  border: 2px solid var(--gray-200);
  border-radius: var(--radius-lg);
  padding: var(--space-md);
  cursor: pointer;
  transition: all 0.3s ease;
  text-align: center;
}

.theme-option:hover {
  border-color: var(--primary-light);
  transform: translateY(-2px);
}

.theme-option.active {
  border-color: var(--primary);
  box-shadow: 0 0 0 3px rgba(79, 70, 229, 0.1);
}

.theme-preview {
  width: 100%;
  height: 80px;
  border-radius: var(--radius-md);
  margin-bottom: var(--space-sm);
}

.blue-theme {
  background: linear-gradient(135deg, #4f46e5 0%, #6366f1 100%);
}

.green-theme {
  background: linear-gradient(135deg, #10b981 0%, #059669 100%);
}

.purple-theme {
  background: linear-gradient(135deg, #8b5cf6 0%, #7c3aed 100%);
}

.theme-option span {
  font-size: 0.9rem;
  font-weight: 600;
  color: var(--gray-700);
}

/* Responsive */
@media (max-width: 1200px) {
  .app-container {
    flex-direction: column;
  }
  
  .app-sidebar {
    width: 100%;
    height: auto;
  }
  
  .sidebar-content {
    height: auto;
  }
  
  .task-form-container,
  .calendar-container-full {
    grid-template-columns: 1fr;
  }
  
  .form-row {
    grid-template-columns: 1fr;
  }
  
  .stats-grid {
    grid-template-columns: 1fr;
  }
  
  .tasks-list {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 768px) {
  .header-content {
    flex-direction: column;
    text-align: center;
    gap: var(--space-md);
  }
  
  .date-navigation {
    width: 100%;
    justify-content: center;
  }
  
  .app-container {
    padding: var(--space-md);
  }
  
  .tasks-summary {
    flex-direction: column;
    gap: var(--space-sm);
  }
  
  .stats-overview {
    grid-template-columns: 1fr;
  }
  
  .metrics-grid {
    grid-template-columns: 1fr;
  }
  
  .theme-options {
    grid-template-columns: 1fr;
  }
}

/* Animaciones */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes scaleIn {
  from {
    opacity: 0;
    transform: scale(0.9);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

/* Utilidades */
.loading {
  animation: pulse 2s infinite;
}

.shimmer {
  background: linear-gradient(90deg, 
    rgba(255,255,255,0) 0%, 
    rgba(255,255,255,0.2) 50%, 
    rgba(255,255,255,0) 100%);
  background-size: 200% 100%;
  animation: shimmer 2s infinite;
}

@keyframes shimmer {
  0% { background-position: -200% 0; }
  100% { background-position: 200% 0; }
}

/* Estados */
:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

/* Focus states */
:focus-visible {
  outline: 2px solid var(--primary);
  outline-offset: 2px;
}

/* Print styles */
@media print {
  .app-sidebar,
  .tasks-header,
  .task-form-container > :first-child,
  .modal-overlay {
    display: none;
  }
  
  .app-container {
    flex-direction: column;
  }
  
  .app-main {
    width: 100%;
  }
  
  .task-card {
    break-inside: avoid;
  }
}/* ========== CORRECCIÓN INPUT HORAS ========== */
.hours-input-container {
  position: relative;
  display: flex;
  align-items: center;
}

.hours-input-container input[type="number"] {
  padding-right: 80px !important;
  width: 100%;
}

/* Ocultar spinners nativos */
input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type="number"] {
  -moz-appearance: textfield;
}

.input-suffix {
  position: absolute;
  right: 40px;
  top: 50%;
  transform: translateY(-50%);
  color: var(--gray-500);
  font-size: 0.9rem;
  pointer-events: none;
  z-index: 1;
}

/* Spinners personalizados */
.hours-spinner {
  position: absolute;
  right: 10px;
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: center;
  gap: 2px;
  z-index: 2;
}

.spinner-btn {
  width: 24px;
  height: 24px;
  background: var(--gray-100);
  border: 1px solid var(--gray-300);
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  font-size: 0.8rem;
  color: var(--gray-600);
  transition: all 0.2s ease;
  user-select: none;
}

.spinner-btn:hover {
  background: var(--gray-200);
  color: var(--primary);
}

.spinner-btn:active {
  background: var(--primary);
  color: white;
}

.spinner-btn.up {
  border-radius: 4px 4px 0 0;
}

.spinner-btn.down {
  border-radius: 0 0 4px 4px;
}

/* Para el modal */
.modal-body .hours-input-container {
  margin-top: 0.5rem;
}

.modal-body .hours-input-container input {
  padding: 0.75rem !important;
}

.modal-body .input-suffix {
  right: 70px;
}

.modal-body .hours-spinner {
  right: 10px;
}